import { Store } from'../core/heropy'

export interface SimpleMovie {
  Title: string
  Year: string
  imdbID: string
  Type: string
  Poster: string
}
interface DetailedMovie {
  Title: string
  Year: string
  Rated: string
  Released: string
  Runtime: string
  Genre: string
  Director: string
  Writer: string
  Actors: string
  Plot: string
  Language: string
  Country: string
  Awards: string
  Poster: string
  Ratings: {
    Source: string
    Value: string
  }
  Metascore: string
  imdbRating: string
  imdbVotes: string
  imdbID: string
  Type: string
  DVD: string
  BoxOffice: string
  Production: string
  Website: string
  Response: string
}

interface State {
  searchText: string
  page: number
  pageMax: number
  movies: SimpleMovie[]
  movie : DetailedMovie
  loading: boolean
  message: string
}

const store = new Store<State>({
  searchText: '',
  page:1,
  pageMax: 1,
  movies: [],
  //영화의 정보를 가져온 뒤에 정보가 갱신되기 때문에 as DetailedMovie추가
  movie: {} as DetailedMovie, //영화 상세 정보
  loading: false,
  message: 'Search for the movie title!'
})

export default store
export const searchMovies = async (page: number) => {
  //loading animation 시작
  store.state.loading = true
  store.state.page = page
  if (page ===1) { // 새로 검색 할 경우 1page로 이동
    store.state.movies = []
    store.state.message = ''
  }
  // 예외 처리 코드 추가
  try {
    // const res = await fetch(`https://omdbapi.com?apikey=7035c60c&s=${store.state.searchText}&page=${page}`) //연습용 apiKey //api 보안 추가 // 제거필요
    const res = await fetch('/api/movie', {
      method: 'POST',  //fetch에서 body를 사용 할 때는 method: 'POST' 추가필요
      // body에는 문자열로 들어가야함
      body: JSON.stringify({
        title: store.state.searchText,
        page
      })
    })
    const { Search, totalResults, Response, Error } = await res.json()
    if (Response === 'True') {
      store.state.movies = [
        ...store.state.movies, //1page 정보
        ...Search //다음 page 정보
      ]
      // 올림 처리
      store.state.pageMax = Math.ceil(Number(totalResults) / 10)
    } else {
      store.state.message = Error
      store.state.pageMax = 1 //pageMax값이 2 이상일 때 목록을 가져오지 못하면 초기화
    }
  } catch (error) {
    console.log('searchMovies error:', error)
  } finally {
    // loading animation 종료
    store.state.loading = false
  }
}
export const getMovieDetails = async (id: string) => {
  try {
    // i parameter는 영화의 id의 정보, plot은 전체 줄거리
    // const res = await fetch(`https://omdbapi.com?apikey=7035c60c&i=${id}&plot=full`) // api 보안 추가 // 제거 필요
    const res = await fetch('/api/movie', {
      method: 'POST',  //fetch에서 body를 사용 할 때는 method: 'POST' 추가필요
      body: JSON.stringify({
        id
      })
    })
    store.state.movie = await res.json()
  } catch (error) {
    console.log('getMovieDetails error:', error)
  }
}